import React, {useEffect, useMemo, useRef, useState} from 'react';
import {Alert, Button, Input} from "antd";
import {connect, useDispatch, useSelector} from "react-redux";
import {Counter} from "../../components/counter";
import {setIsLoading} from '../../store/models/counterSlice'
import Marquee from "react-fast-marquee";

const Notice = (props) => {
    const dispatch = useDispatch()

    function getVuex() {
        dispatch(setIsLoading({msg: '', isL: false}))
    }

    let [count, setCount] = useState(0);

    function getCount() {
        setCount(count += 1)
    }

    //
    useEffect(() => {
        console.log("")
    }, [])

    //count 
    useEffect(() => {
        console.log("ok--count ")
    }, [count])
    //useMemo 
    useMemo(() => {
        console.log('memo')
    }, [count])

    // useRef  
    let refInput = useRef(null)
    const getInputValue = () => {
        console.log(':')
        console.log(refInput)
        refInput.current.focus();
    }

    return (
        <div>
            <h2>useEffect,useMemo</h2>
            <Button type='primary' onClick={getCount}>count</Button>
            <h3>{count}</h3>

            <h2>Redux</h2>
            <Button type='primary' onClick={getVuex}>Redux</Button>
            <Counter/>

            <h2>useRef</h2>
            <Button type='primary' onClick={getInputValue}></Button>
            <Input ref={refInput} placeholder=''  allowClear/>

        </div>
    );
};

let select = (state) => {
    return {
        type: state.type
    }
}
export default connect(select)(Notice);
